import axios from '../authentication/customAxios';

export function getAllAdmin()  {
    return axios.get('/get-all-admin');
}

export const getAdmin = (id) => {
    return axios.get(`/get-admin/${id}`);
}

export const updateAdmin = (id, data) => {
    return axios.put(`/update-admin/${id}`, data);
}

export const addAdmin = (data) => {
    return axios.post('/add-admin', data);
}

export const getAllDriver = () => {
    return axios.get('/get-all-driver');
}

export const saveDriver = (data) => {
    return axios.post('/save_driver', data);
}

export const getDriver = (id) => {
    return axios.get(`/get-single-driver/${id}`);
}

export const updateDriver = (id, data) => {
    return axios.put(`/update-driver/${id}`, data);
}