import React, { useState } from 'react';
import { Route, Redirect } from  "react-router-dom";
import Header from '../../home/includes/header';
import Sidebar from '../../home/includes/sidebar';
import jwt_decode from "jwt-decode"



const  PrivateRoute: React.FC<{
        component: React.FC;
        path: string;
        exact: boolean;
    }> = (props) => {
    // const [checkAuth, setAuth] = useState(false);  
    // const condition = ()=> {
    //     return setAuth(true)
    // }  
    // console.log("Condition===========>",condition);
    // return <Route  path={props.path}  exact={props.exact} component={props.component} />
    function getJWTExpireDate(token) {
        if (token) {
          try {
            const [, payload] = token.split('.');
            const { exp: expires } = JSON.parse(window.atob(payload));
            if (typeof expires === 'number') {
              return new Date(expires * 1000);
            }
          } catch {
            // ignore
          }
        }
        return null;
      }
    let token = localStorage.getItem('authToken');
    // let decodedToken = jwt_decode(token);
    let currentDate = new Date();
    let condition = false;
    // console.log(decodedToken.exp * 1000 +"<" + currentDate.getTime());
    
    let conditionCheck = getJWTExpireDate(token);
    console.log("condcheck==>",conditionCheck);
    console.log("Current===>",currentDate);
    if (conditionCheck > currentDate) {
        condition = true;
    }
    console.log("Condition=======>",condition);
    return  condition ? (<div className="main-wrapper">
    <Header />
    <Sidebar /><Route  path={props.path}  exact={props.exact} component={props.component} /> </div>) : 
   (<Redirect  to="/login"  />);
};

