import React, { useState } from "react";
import {loginService as login} from '../services/authentication/AuthService';
import { useHistory } from "react-router-dom";
import MuiAlert from "@material-ui/lab/Alert";
import 'react-toastify/dist/ReactToastify.css';

import { store } from 'react-notifications-component';
import "react-loader-spinner/dist/loader/css/react-spinner-loader.css";
import Loader from "react-loader-spinner";

function Alert(props) {
	return <MuiAlert elevation={6} variant="filled" {...props} />;
}


function Login(props) { 
	const history = useHistory();

	const [username, setUsername] = useState("");
	const [password, setPassword] = useState("");
	const [error, setError] = useState("");
	let   [checkLoad, setCheckload] = useState(false);
	const submitForm = () => {
		console.log("checkLoad===>",checkLoad);
		if (username === "" || password === "") {
		  setError("Fields are required");
		  console.log(error);
		  return;
		}
		setCheckload(true);
		login({username: username, password: password}).then(res=>{
			//console.log("okkkkkkk");
			if(res.status===200){
			    localStorage.setItem('authToken', res.data.result.authToken);
				store.addNotification({
					title: "Welcome!",
					message: "Admin",
					type: "success",
					insert: "top",
					container: "top-right",
					animationIn: ["animate__animated", "animate__fadeIn"],
					animationOut: ["animate__animated", "animate__fadeOut"],
					dismiss: {
					  duration: 5000,
					  onScreen: true
					}
				  });
				  setCheckload(false);
				  history.push("/");
			}
		}).catch(err=>{
			if(err)
			{
			  setCheckload(false);		
			  console.log("=========>",err);		
			  console.log(err?.response?.data?.message);
			  store.addNotification({
				title: "Welcome!",
				message: err?.response?.data?.message,
				type: "danger",
				insert: "top",
				container: "top-right",
				animationIn: ["animate__animated", "animate__fadeIn"],
				animationOut: ["animate__animated", "animate__fadeOut"],
				dismiss: {
				  duration: 5000,
				  onScreen: true
				}
			  });
			}
		})
	  };
	
        return (
            <div class="login-wrapper">
				{ checkLoad && (<Loader
					type="Puff"
					color="#00d0f1"
					height={100}
					width={100}
					timeout={10000} 
					className="ReactLoder"
				/>)}
            	<div class="container">
                	<div class="loginbox">
                    	<div class="login-left">
							<img class="img-fluid" src="assets/img/logo-fetch.png" alt="Logo" />
                        </div>
                        <div class="login-right">
							<div class="login-right-wrap">
								<h1>Login</h1>
								<p class="account-subtitle">Access to our dashboard</p>
								{/* <div className="alert-danger text-center">
									<p>{state.apiError}</p>
								</div> */}
								<form autoComplete = "off" method="post">
									{error && (
									<Alert severity="error" onClick={() => setError(null)}>
									{props.error || error}
									</Alert>
									)}
									<div class="form-group">
										<input className = {`form-control`} type="text" name="username"  onChange={e => setUsername(e.target.value)} placeholder="User Name"/>
										
									</div>
									<div class="form-group">
											<input className={`form-control`} type="password" name="password" onChange={e => setPassword(e.target.value)} placeholder = "Password" />
									</div>
									<div class="form-group">
										<button class="btn btn-primary btn-block" type="button" onClick={submitForm}>Login</button>
									</div>
								</form>
								
								<div class="text-center forgotpass"><a href="forgot-password.html">Forgot Password?</a></div>
							
							</div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
export default Login; 